import { Button } from "./styled"
import { theme } from "../../../theme";

const Buttons = ({ tasks, hideDone, toggleHideDone, setAllDone }) => (

    <div>
        {tasks.length > 0 && (
            <>
                <Button
                    theme={theme}
                    onClick={toggleHideDone}
                    >
                    {hideDone ? "Poka " : "Ukryj "}
                    ukoczone
                </Button>
                <Button
                    theme={theme}
                    onClick={setAllDone}
                    disabled={tasks.every(({ done }) => done)}
                >
                    Ukocz wszystkie
                </Button>
            </>
        )}
    </div>
);

export default Buttons;